import { IconButton } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import ReactiveDialog from "../reactivedialog/ReactiveDialog";
import SearchIcon from '@material-ui/icons/Search';
import useStyles from "./PexelsSearchDialogStyle";
import ReactiveTextField from "../reactivetextfield/ReactiveTextField";
import { query } from "./PexelsManager";
import { Photo } from "pexels";
import Alert from '@material-ui/lab/Alert';
import { useTimeout } from "../timeout/Timeout";
import { importImageUrl } from "../../helper/CanvasHelper";
import { Art } from "../../structures/Art";

interface PexelsArtDialogProps {
    isOpen: boolean,
    onClose: Function,
    onApply: (art: Art) => void
}

export default function PexelsSearchDialog(props: PexelsArtDialogProps) {
    const classes = useStyles();

    const [isRateLimitExceeded, setRateLimitExceeded] = useState<boolean>(false);
    const [isErrorShown, setErrorShown] = useState<boolean>(false);

    const [page, setPage] = useState<number>(0);
    const [queryText, setQueryText] = useState<string>("");
    const [loadedPhotos, setLoadedPhotos] = useState<Photo[]>([]);

    const timeout = useTimeout();

    const handleSearch = () => {
        query(queryText, page, (photos) => {
            if (isRateLimitExceeded) {
                setRateLimitExceeded(false);
            }
            setPage(page + 1);
            if (page > 1) {
                setLoadedPhotos(loadedPhotos.concat(photos));
            } else {
                setLoadedPhotos(photos);
            }
        }, () => {
            console.log("  ");
            if (!isRateLimitExceeded) {
                setRateLimitExceeded(true);
            }
            setErrorShown(true);
            timeout(3000, () => {
                setErrorShown(false)
            });
        }, (percentage) => {
            
        });
    }

    const handleNewSearch = () => {
        setPage(0);
        handleSearch();
    }

    const handleImageClick = (photo: Photo) => {
        importImageUrl(photo.src.original, (art) => {
            props.onApply(art);
        })
    }

    const handleClose = () => {
        props.onClose();
    }

    useEffect(() => {
        const listener = (e: KeyboardEvent) => {
            if (e.key === "Enter") {
                handleNewSearch();
            }
        };
        window.addEventListener("keydown", listener);
        return () => {
            window.removeEventListener("keydown", listener);
        }
    });
    return (
        <ReactiveDialog
            title="   Pexels"
            paperStyle={{
                width: "72%",
                maxWidth: "72%"
            }}
            contentStyle={{
                textAlign: "center"
            }}
            isOpen={props.isOpen}
            onScroll={(e) => {
                const div = e.currentTarget;
                if (div.offsetHeight + div.scrollTop >= div.scrollHeight) {
                    handleSearch();
                }
            }}
            onClose={() => handleClose()}>
            <div className={classes.searchContainer}>
                <ReactiveTextField
                    className={classes.searchField}
                    text={queryText}
                    type="text"
                    label=""
                    autocomplete="off"
                    onChange={(e) => {
                        setQueryText(e.target.value)
                    }} />
                <div className={classes.searchButtonWrapper}>
                    <IconButton
                        type="submit"
                        aria-label="search"
                        onClick={() => handleNewSearch()}>
                        <SearchIcon
                            htmlColor="#FFFFFF8a" />
                    </IconButton>
                </div>
            </div>

            {(loadedPhotos.length > 0 && !isRateLimitExceeded) &&
                <div className={classes.searchResultsWrapper}>
                    <h5> </h5>
                </div>}
            {isErrorShown &&
                <Alert
                    className={classes.error}
                    variant="filled" severity="error">
                      .   
              </Alert>}
            <div>
                <div className={classes.photoLineContainer}>
                    {loadedPhotos.map((photo, index) => {
                        return getHorizontalPosition(loadedPhotos, index) === 0 &&
                            <img
                                className={classes.photo}
                                onClick={() => handleImageClick(photo)}
                                key={photo.id}
                                src={photo.src.medium}
                                alt={": " + photo.photographer}
                                width="98%" />
                    })}
                </div>

                <div className={classes.photoLineContainer}>
                    {loadedPhotos.map((photo, index) => {
                        return (getHorizontalPosition(loadedPhotos, index) === 1 &&
                            <img
                                className={classes.photo}
                                onClick={() => handleImageClick(photo)}
                                key={photo.id}
                                src={photo.src.medium}
                                alt={": " + photo.photographer}
                                width="98%" />)
                    })}
                </div>

                <div className={classes.photoLineContainer}>
                    {loadedPhotos.map((photo, index) => {
                        return (getHorizontalPosition(loadedPhotos, index) === 2 &&
                            <img
                                className={classes.photo}
                                onClick={() => handleImageClick(photo)}
                                key={photo.id}
                                src={photo.src.medium}
                                alt={": " + photo.photographer}
                                width="98%" />)
                    })}
                </div>
            </div>
        </ReactiveDialog>
    );
}

function getHorizontalPosition(photos: Photo[], index: number): number {
    const startBlockIndex = index - index % 3;
    const photo1 = photos[startBlockIndex].height / photos[startBlockIndex].width;
    const photo2 = photos[startBlockIndex + 1].height / photos[startBlockIndex + 1].width;
    const photo3 = photos[startBlockIndex + 2].height / photos[startBlockIndex + 2].width;
    const sortedIndices = [3];
    if (photo1 <= photo2) {
        if (photo2 <= photo3) {
            sortedIndices[0] = 0;
            sortedIndices[1] = 1;
            sortedIndices[2] = 2;
        } else {
            if (photo3 < photo1) {
                sortedIndices[0] = 2;
                sortedIndices[1] = 0;
            } else {
                sortedIndices[0] = 0;
                sortedIndices[1] = 2;
            }
            sortedIndices[2] = 1;
        }
    } else {
        if (photo1 <= photo3) {
            sortedIndices[0] = 1;
            sortedIndices[1] = 0;
            sortedIndices[2] = 2;
        } else {
            if (photo3 < photo2) {
                sortedIndices[0] = 2;
                sortedIndices[1] = 1;
            } else {
                sortedIndices[0] = 1;
                sortedIndices[1] = 2;
            }
            sortedIndices[2] = 0;
        }
    }
    const newIndex = (index - (-Math.floor(index / 3 + 1) % 3) + 3) % 3;
    console.log(startBlockIndex + "- " + index + ": " + newIndex + " & " + Math.round(index / 3 + 1));
    return sortedIndices[newIndex];
}